<template>
  <el-row class="set-Lang">
    <el-radio-group v-model="lang">
      <el-radio v-for="(item, index) in langList"
                :key="index"
                :label="item.value">{{item.name}}</el-radio>
    </el-radio-group>
    <!--<span class="iconfont iconquanping" @click="screenFullClick"></span>-->
  </el-row>
</template>

<script>
import screenFull from "screenfull";
export default {
  name: "set-lang",
  data() {
    return {
      isFullscreen: false,
      lang: 'df'
    };
  },
  computed: {
    langList: {
      get() {
        return this.$t("settingData.lang.list");
      }
    }
  },
  watch: {
    lang (val) {
      this.$i18n.locale = val;
    }
  },
  methods: {
    screenFullClick() {
      if (!screenFull.enabled) {
        return false;
      }
      screenFull.toggle();
    }
  }
};
</script>
