<template>
  <div class="word-clock">
    <el-row class="smallClock">
      <clock></clock>
    </el-row>
    <el-row class="bigClock">
      <SetLang class="ptb-10 text-center"/>
      <el-row class="clock-center">
        <Second :second="second"/>
        <Minute :minute="minute"/>
        <Hour :hour="hour"/>
        <!--<Apm :apm="apm" />-->
        <Week :week="week"/>
        <!--<Day :day="day" />-->
        <!--<Month :month="month" />-->
      </el-row>
    </el-row>
  </div>
</template>

<script>
import clock from '@/components/clock'
import Second from './components/Second.vue'
import Minute from './components/Minute.vue'
import Hour from './components/Hour.vue'
import Apm from './components/Apm.vue'
import Week from './components/Week.vue'
import Day from './components/Day.vue'
import Month from './components/Month.vue'
import SetLang from './components/SetLang'

export default {
  name: 'word-clock',
  components: {
    clock,
    Second,
    Minute,
    Hour,
    Apm,
    Week,
    Day,
    Month,
    SetLang
  },
  data () {
    return {
      second: '',
      minute: '',
      hour: '',
      apm: '',
      week: '',
      day: '',
      month: '',
      year: ''
    }
  },
  methods: {
    start () {
      setInterval(() => {
        let data = new Date()
        this.second = data.getSeconds()
        this.minute = data.getMinutes()
        this.hour = data.getHours()
        this.week = data.getDay()
        this.day = data.getDate()
        this.month = data.getMonth() + 1
        this.year = data.getFullYear()
        if (this.hour > 12) {
          this.apm = 2
        } else {
          this.apm = 1
        }
      }, 1000)
    }
  },
  created () {
    this.start()
  }
}
</script>
<style lang="scss">
.word-clock {

  .smallClock {
    display: none;
  }

  .bigClock {
    display: block;
  }

  .clock-center {
    position: relative;
    margin-top: 280px;
    height: 530px;

    ul {
      list-style-type: none;
      padding: 0;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: 40px auto;
      height: 180px;
      width: 60px;
      transition: 0.1s 0.1s ease-in;

      li {
        position: absolute;
        height: 180px;
        width: 60px;
        color: #000;
        text-align: center;
        font-size: 12px;
        line-height: 20px;

        &.hover {
          text-shadow: rgb(255, 255, 255) 0 0 10px,
          rgb(255, 255, 255) 0 0 20px, rgb(255, 0, 222) 0 0 30px,
          rgb(255, 0, 222) 0 0 40px, rgb(255, 0, 222) 0 0 70px,
          rgb(255, 0, 222) 0 0 80px, rgb(255, 0, 222) 0 0 100px;
        }
      }
    }
  }
}

@media screen and (max-device-width: 400px) {
  .word-clock {
    .smallClock {
      display: block;
      text-align: center;
      padding-top: 150px;
    }

    .bigClock {
      display: none;
    }
  }
}
</style>
